body {
  margin: 0;
  padding: 0;
}




.run {
  display: flex;
  flex-direction: row;
  grid-gap: 1em;
  gap: 1em;
  margin: 1em 0;
  border-radius: 169px 0.375rem 0.375rem 169px;
  width: 70rem;
  border: 1px solid rgba(0, 0, 0, 0.125);
  font-family: "Krub", sans-serif;
  box-shadow: 0 0 5px lightgrey;
}

.run-image {
  width: auto;
  height: 21rem;
  border-radius: 50%;
}

.run-body {
  padding: 2em;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  grid-gap: 1em;
  gap: 1em;
  width: 100%;
}

.run-body p {
  margin: 0;
}

.run-heading {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.run-desc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  height: 9em;
}

.run-desc .list-group-item {
  padding-left: 0;
}

.run-heading .run-id {
  background-color: #d83a01c3;
  color: white;
  font-size: 1.5rem;
  font-weight: 800;
  margin: -0.3em 0.5em 0 0;
  width: 2em;
  height: 2em;
  border-radius: 50%;
  text-align: center;
  padding-top: 0.2em;
}

.run-heading h3 {
  margin-right: auto;
}

.run-heading .detail-button {
  background-color: #d83a01c3;
  color: white;
  height: 2em;
  border-radius: 0.375rem;
  font-size: 15px;
  font-weight: 500;
  border: none;
}

.run-heading .detail-button:hover {
  cursor: pointer;
  background-color: #3e363e;
  transition: 0.5s;
}

.join-button {
  height: 3em;
  width: 10em;
  background-color: #d83a01c3;
  border: none;
  box-shadow: 0 0 5px lightgrey;
  color: white;
  border-radius: 0.375rem;
  font-weight: 800;
  align-self: flex-end;
}

.join-button:hover {
  font-weight: 800;
  background-color: #d83a01ee;
  transition: 0.5s;
}

.join-button[disabled] {
  background-color: #3e363e64;
  font-weight: 300;
}

.run-modal {
  color: #3e363e;
  font-family: "Krub", sans-serif;
}

.route-modal-header {
  background-color: #d83a01c3;
}

.route-modal-header .modal-title {
  font-size: 35px;
  color: white;
  font-weight: 600;
  text-align: center;
  width: 100%;
}

.route-modal-header .btn-close {
  color: white;
}

.route-modal-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
  width: 100%;
  padding: 1em 3em;
}

.route-modal-message p {
  font-size: 20px;
  font-weight: 200;
  text-align: start;
  margin: 0;
}

.route-modal-message .run-info {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  grid-gap: 2em;
  gap: 2em;
}

.run-modal .btn:hover {
  background-color: white;
  color: black;
}

.run-path .run-info-image {
  width: 3em;
  height: 3em;
}

#run-path {
  border-radius: 2rem;
  box-shadow: 0 0 3px #3e363ea8;
}

.profile-section {
  padding-bottom: 3em;
}

.profile-pic {
  height: 330px;
  border-radius: 5px;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-header {
  width: 70rem;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  border: 1px solid lightgrey;
  margin: 3em 0;
  padding: 1.5em;
  border-radius: 5px;
  font-family: "Krub", sans-serif;
  box-shadow: 0 0 5px lightgrey;
}

.profile-info {
  width: 63%;
  display: flex;
  flex-direction: column;
  grid-gap: 25px;
  gap: 25px;
}

.profile-info h1 {
  font-weight: 200;
  font-size: 70px;
  margin: 0;
}

.profile-info h4 {
  font-weight: 200;
  font-size: 20px;
  margin: 0;
  color: gray;
}

.profile-info p {
  font-weight: 600;
  font-size: 35px;
  margin: -20px -10px 0 0;
  text-align: end;
  color: #d83b01;
}


.profile-welcome {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.profile-welcome .profile-actions {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  grid-gap: 1em;
  gap: 1em;
  padding: 0px;
}

.profile-welcome .profile-actions span {
  font-size: 35px;
}

.profile-actions:hover {
  cursor: pointer;
}

.profile-info table {
  table-layout: fixed;
  width: 100%;
  text-align: center;
  margin: 0;
}

.profile-info tbody {
  font-size: 70px;
}

.profile-info .unit {
  font-size: 30px;
}

.profile-stats {
  font-family: "Krub", sans-serif;
}
.profile-stats button {
  font-weight: 500;
  color: #495057;
}

.profile-stats button:hover {
  color: white;
  background-color: #3e363e;
}

.runs-tab {
  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
}

.empty-runs-container {
  padding: 2em;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 2em;
  gap: 2em;
}

.empty-runs-container h3{
  font-weight: 300;
  text-align: center;
}

.empty-runs-container .btn {
  width: 20rem;
  height: 7rem;
  color: white;
  font-size: 30px;
  align-self: center;
  border-radius: 0.5rem;
}

.empty-runs-container .btn:hover {
  border: 0.5px solid #3e363e;
}


@media screen and (max-width: 600px) {

  .profile-header {
   width: 95%;
   height: 20%;
   flex-wrap: wrap;
   font-size: 10px;
   flex-direction: row;
  }
  .profile-pic {
   height: 150px;
   width: 150px;
   margin: 5px;
  }
 
  .profile-info tbody {
    font-size: 20px;
  }

  .profile-info .unit {
    font-size: 20px;
  }

  .profile-info h1 {
    font-size: 50px;
  }

  .profile-stats {
    display: flex;
    flex-direction: column;
   
  }

  .run {
    display: flex;
    flex-direction: column;
    width: 95%;
    padding-left: 10px;
  }

  .run-image {
    height: 150px;
    width: 150px;
    margin-left: 10%;
    margin-top: 5%;
    padding: 0
  }
 
}
.map-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  grid-gap: 2em;
  gap: 2em;
  padding: 0 5em;
  font-family: "Krub", sans-serif;
}

.map-info {
  width: 30vw;
  color: #3e363e;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.map-info h2 {
  font-size: 60px;
  font-weight: 200;
}

.map-info p {
  font-size: 20px;
  font-weight: 300;
  
}

.map-info p a {
  color: #3e363e;
}

.map-info .material-symbols-rounded {
  font-size: 150px;
  width: 100%;
  color: #3e363e;
  font-variation-settings: "FILL" 0, "wght" 200, "GRAD" 0, "opsz" 48;
  animation: move 0.5s infinite alternate-reverse ease-in-out;
  position: relative;
}

@-webkit-keyframes move {
  0% {
    bottom: 10px;
  }
  100% {
    bottom: -10px;
  }
}

@keyframes move {
  0% {
    bottom: 10px;
  }
  100% {
    bottom: -10px;
  }
}

.map {
  border-radius: 1rem;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.263);
  box-shadow: 0 0 5px lightgrey;
}


.circle {
  background-color: #d83b01;
  border-radius: 50%;
  color: #fff;
  height: 3em;
  position: relative;
  width: 3em;
}

.circleText {
  text-align: center;
  font-size: 15px;
  font-weight: 500;
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
}

.hover {
  background-color: #0078d4;
}

#button-tooltip .tooltip-inner{
  max-width: 400px;
}

#button-tooltip p {
  margin: 0px;
  font-size: 18px;
  text-align: start;
}
#find-run-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
  margin-top: 2em;
  font-family: "Krub", sans-serif;
  padding-bottom: 3em;
}

#available-runs {
  color: #3e363ebc;
  width: 100%;
  padding: 1em 5em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

#available-runs h2 {
  font-size: 50px;
  font-weight: 800;
}

#available-runs p {
  font-size: 20px;
  font-weight: 300;
}

.joining-status-header {
  align-self: center;
}

.joining-status-header .modal-title {
  font-family: "Krub", sans-serif;
  font-size: 30px;
  font-weight: 700;
}

.joining-status-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
}

.joining-status-message p {
  font-size: 18px;
  padding: 0 2em;
  text-align: center;
  margin: 0;
}

.status-spinner {
  width: 5em;
  height: 5em;
  margin-bottom: 2em;
}

.forms {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2em;
  font-family: "Krub", sans-serif;
}

.form-text {
  color: #3e363e;
}

.form-container {
  width: 40rem;
  border-radius: 0.375rem;
  border: 1px solid rgba(0, 0, 0, 0.175);
  padding: 1em;
  align-self: center;
}

.form-container-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.form-container-text h3 {
  font-size: 40px;
  text-align: center;
}

.form-container-text p {
  font-size: 20px;
  text-align: center;
  width: 70%;
}

.date-picker {
  width: 100%;
  height: 2.3em;
  border-radius: 0.378rem;
  border: 1px solid rgba(0, 0, 0, 0.175);
  padding-left: 0.75rem;
}

.sign-in {
  width: 25rem;
}

.btn {
  
    height: 3em;
    width: 7em;
    background-color: #d83a01c3;
    border: none;
    box-shadow: 0 0 5px lightgrey;
    color: white;
    border-radius: 0.375rem;
    font-weight: 800;
    align-self: flex-end;
  }
  
  .btn:hover {
    font-weight: 800;
    background-color: #d83a01ee;
    transition: 0.5s;
  }
  
  .btn[disabled] {
    background-color: #3e363e64;
    font-weight: 300;
  }
  
.home {
  height: 92vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  grid-gap: 2em;
  gap: 2em;
  padding: 2em;
  font-family: "Krub", sans-serif;
  border-top: 1px solid lightgrey;
  color: #3e363e;
}

.home .home-intro {
  display: flex;
  align-items: center;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
}

.home h2 {
  font-size: 90px;
  font-weight: 100;
  text-align: center;
}

.home p {
  font-size: 20px;
  font-weight: 200;
  text-align: center;
  max-width: 90%;
}

.logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.logo {
  width: 100%;
  height: 100%;
}

.square-logo {
  display: none;
}

.home-actions {
  margin-top: 0.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 1em;
  gap: 1em;
  flex-direction: row;
  font-size: 50px;
  font-weight: 100;
}

.home-actions a {
  color: #3e363e;
  text-decoration: none;
  background-color: #3e363e22;
  width: 15rem;
  border-radius: 0.375rem;
  border: 1px solid lightgray;
  text-align: center;
  padding: 10px 0;
  box-shadow: 0 0 2px lightgrey;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home-actions a:hover {
  background-color: #3e363e10;
  transition: 0.5s;
}

.home-actions a p {
  font-size: 25px;
  max-width: 100%;
}

.underline {
  text-underline-offset: 0.2em;
  -webkit-text-decoration: underline 1px rgba(0, 0, 0, 0);
          text-decoration: underline 1px rgba(0, 0, 0, 0);
  transition: -webkit-text-decoration-color 400ms;
  transition: text-decoration-color 400ms;
  transition: text-decoration-color 400ms, -webkit-text-decoration-color 400ms;
}

.underline:hover {
  text-underline-offset: 0.2em;
  -webkit-text-decoration-color: darkgrey;
          text-decoration-color: darkgrey;
}

@media screen and (max-width: 1280px) {
  .home {
    grid-gap: 1em;
    gap: 1em;
  }

  .home-intro h2 {
    font-size: 70px;
  }

  .home-intro p {
    font-size: 1.2rem;
  }

  .home-actions {
    grid-gap: 1em;
    gap: 1em;
    font-size: 40px;
  }

  .home-actions a {
    width: 13rem;
    font-weight: 300;
  }

  .home-actions a p {
    font-size: 1.2rem;
    max-width: 80%;
  }
}

@media screen and (max-width: 991px) {
  .home {
    height: 100%;
    flex-direction: column-reverse;
    padding-bottom: 3em;
    padding-right: 0;
    padding-left: 0;
  }

  .logo-container {
    width: 25rem;
  }

  .home-intro h2{
    width: 80%;
  }

  .home-intro p {
    width: 80%;
  }

  .home-actions {
    margin-top: 1rem;
  }

  .home-actions a p {
    font-size: 1rem;
    max-width: 80%;
  }
}

@media screen and (max-width: 600px) {
  .home {
    padding-top: 0;
  }

  .logo {
    display: none;
  }

  .logo-container {
    overflow: hidden;
    width: 100vw;
    background-color: #e6795d;
  }
  .square-logo {
    display: block;
    height: 100%;
    margin-top: -9.2em;
    margin-bottom: -3em;
  }

 .home-intro h2 {
    font-size: 45px;
  }

 .home-intro p {
    font-size: 1rem;
  }

   .home-actions a {
    width: 9rem;
    font-size: 20px;
  }

  .home-actions a p {
    font-size: 0.9rem;
  }
}

#navigation {
  font-family: "Krub", sans-serif;
  margin: 0;
  padding: 0;
}

.navbar-brand img {
  width: 2em;
  height: 2em;

}

.nav-link {
  padding: 1em;
}

.nav-link:hover {
  background-color: #d83a01c3;
  color: white;
  transition: all 0.5s;
}


